import React, { Component } from 'react'
import './style.less'
import HeaderBar from '../../components/HeaderBar'
import StationList from '../../components/StationList'
import StationSelector from '../../components/StationSelector'
import Card from '../../components/Card'
import http from '../../api/http'

class Arrive extends Component {
    constructor(props) {
        super(props)
        this.state = {
            nodelist: [
                ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''],
                ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''],
                ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '']
            ],
            show: false,
            node: '',
            next: []
            // next: [{LineNo:"1",Name:"",No:"116",StationType:1,UpStream:{Terminal:"",ArrivedTimes:["19:51:46","19:54:21","19:56:56"]},DownStream:{Terminal:"",ArrivedTimes:["19:51:31","19:54:06","19:56:41"]}},{LineNo:"2",Name:"",No:"212",StationType:1,UpStream:{Terminal:"",ArrivedTimes:["19:55:09","20:08:54","20:15:54"]},DownStream:{Terminal:"",ArrivedTimes:["19:51:09","19:52:08","19:56:08"]}}]
        }
        this.showlist = this.showlist.bind(this)
        this.choosenode = this.choosenode.bind(this)
    }

    showlist (key) {
        this.setState({
            show: true
        })
    }

    choosenode (node) {
        this.setState({
            show: false,
            node
        })
        http.post(`http://172.29.42.39/Station/Home/GetNextStation?stationName=${encodeURI(node)}`)
        .then(data => {
            this.setState({
                next: data.data.Data
            })
        })
        .catch(err => {
            console.log(err)
        })
    }

    componentDidMount () {
        http.post(`http://172.29.42.39/Station/Home/GetNextStation?stationName=${encodeURI('')}`)
        .then(data => {
            this.setState({
                next: data.data.Data
            })
        })
        .catch(err => {
            console.log(err)
        })
    }

    render () {
        let num = ['', '', '']
        return (
            <div className='arrive'>
                <HeaderBar title=''/>
                <StationSelector station={this.state.node} showlist={this.showlist}/>
                <div className='arrive__list'>
                    {
                        this.state.next.map((item, index) => {
                            return (
                                <div key={index}>
                                    <Card title={`${item.DownStream.Terminal}`} type='right'>
                                        {
                                            item.DownStream.ArrivedTimes.map((time, index) => {
                                                return (
                                                    <div key={time}>{`${num[index]}: ${time}`}</div>
                                                )
                                            })
                                        }
                                    </Card>
                                    <Card title={`${item.UpStream.Terminal}`} type='left'>
                                        {
                                            item.UpStream.ArrivedTimes.map((time, index) => {
                                                return (
                                                    <div key={time}>{`${num[index]}: ${time}`}</div>
                                                )
                                            })
                                        }
                                    </Card>
                                </div>
                            )
                        })
                    }
                </div>
                {!!this.state.show &&
                    <StationList nodelist={this.state.nodelist}
                                choosenode={this.choosenode}/>
                }
            </div>
        )
    }
}

export default Arrive