import React, { Component } from 'react'
import './style.less'
import StationList from '../../components/StationList'
import StationSelect from './station-select'
import Recommand from './recommand'
import http from '../../api/http'

class Path extends Component {
    constructor(props) {
        super(props)
        this.state = {
            nodelist: [
                ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''],
                ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''],
                ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '']
            ],
            show: false,
            begin: '',
            end: '',
            hash: '',
            // lines: [{LinePoint:"------------",TransferLine:"1",LastSendTime:"23:36:09"},{LinePoint:"---------------",TransferLine:"1-5-1",LastSendTime:"22:45:09"}]
            lines: []
        }
        this.showlist = this.showlist.bind(this)
        this.choosenode = this.choosenode.bind(this)
    }

    showlist (key) {
        this.setState({
            show: true,
            hash: key
        })
    }

    choosenode (node) {
        if (node) {
            let b = ''
            let e = ''
            if (this.state.hash === 'begin') {
                b = node
                e = this.state.end
                this.setState({
                    begin: node,
                    show: false
                })
            } else {
                b = this.state.begin
                e = node
                this.setState({
                    end: node,
                    show: false
                })
            }
            if (b && e) {
                console.log(b, e)
                http.post(`http://172.29.42.39/Station/Home/GetSearchResult?startNo=${encodeURI(b)}&endNo=${encodeURI(e)}`)
                .then(data => {
                    this.setState({
                        lines: data.data.Data.Lines
                    })
                })
                .catch(err => {
                    console.log(err)
                })
            }
        } else {
            this.setState({
                show: false
            })
        }
    }

    render () {
        return (
            <div className='path'>
                <StationSelect showlist={this.showlist} begin={this.state.begin} end={this.state.end}/>
                <Recommand lines={this.state.lines}/>

                {!!this.state.show &&
                    <StationList nodelist={this.state.nodelist}
                                 choosenode={this.choosenode}/>
                }
            </div>
        )
    }
}

export default Path