import React, { Component } from 'react'
import './style.less'
import HeaderBar from '../../components/HeaderBar'
import StationList from '../../components/StationList'
import StationSelector from '../../components/StationSelector'
import Card from '../../components/Card'
import { inject, observer } from 'mobx-react'
import http from '../../api/http'

@inject('mapStore')
@observer
class Around extends Component {
    constructor(props) {
        super(props)
        this.state = {
            nodelist: [
                ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''],
                ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''],
                ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '']
            ],
            show: false,
            node: '',
            food: [],
            hotel: [],
            shopping: []
        }
        this.showlist = this.showlist.bind(this)
        this.choosenode = this.choosenode.bind(this)
        this.renderList = this.renderList.bind(this)
        this.getAddress = this.getAddress.bind(this)
    }

    componentDidMount () {
        this.getAddress('')
    }

    showlist (key) {
        this.setState({
            show: true
        })
    }

    choosenode (node) {
        this.setState({
            show: false,
            node
        })
        this.getAddress(node)
    }

    getAddress (node) {
        http.post(`http://restapi.amap.com/v3/geocode/geo?key=7f794c73a70f7476572d350b7653562a&address=${node}&city=`)
        .then(data => {
            this.renderList(data.data.geocodes[0].location)
        })
    }

    renderList (location) {
        console.log(location)
        let lng = location.split(',')[0]
        let lat = location.split(',')[1]
        http.post(`http://restapi.amap.com/v3/geocode/regeo?key=7f794c73a70f7476572d350b7653562a&location=${lng},${lat}&poitype=&radius=1500&extensions=all&batch=false&roadlevel=0`)
        .then(data => {
            console.log(data)
            this.setState({
                food: data.data.regeocode.pois
            })
        })
        http.post(`http://restapi.amap.com/v3/geocode/regeo?key=7f794c73a70f7476572d350b7653562a&location=${lng},${lat}&poitype=&radius=1500&extensions=all&batch=false&roadlevel=0`)
        .then(data => {
            this.setState({
                hotel: data.data.regeocode.pois
            })
        })
        http.post(`http://restapi.amap.com/v3/geocode/regeo?key=7f794c73a70f7476572d350b7653562a&location=${lng},${lat}&poitype=&radius=1500&extensions=all&batch=false&roadlevel=0`)
        .then(data => {
            this.setState({
                shopping: data.data.regeocode.pois
            })
        })
    }

    render () {
        const FoodList = this.state.food.map((item, index) => {
            if (index <= 5) {
                return (
                    <p key={index} className="around__item">{item.name}</p> 
                )
            }
        })
        const HotelList = this.state.hotel.map((item, index) => {
            if (index <= 5) {
                return (
                    <p key={index} className="around__item">{item.name}</p> 
                )
            }
        })
        const ShoppingList = this.state.shopping.map((item, index) => {
            if (index <= 5) {
                return (
                    <p key={index} className="around__item">{item.name}</p> 
                )
            }
        })
        return (
            <div className='around'>
                <HeaderBar title=''/>
                <StationSelector station={this.state.node} showlist={this.showlist}/>
                <div className='around__list'>
                    <Card title='' type='food'>
                    {FoodList}
                    </Card>
                    <Card title='' type='hotel'>
                    {HotelList}
                    </Card>
                    <Card title='' type='list'>
                    {ShoppingList}
                    </Card>
                </div>
                {!!this.state.show &&
                    <StationList nodelist={this.state.nodelist}
                                 choosenode={this.choosenode}/>
                }
            </div>
        )
    }
}

export default Around